import React, { useState } from "react";
import {
  Wrap,
  Title,
  Line,
  HeaderWrap,
  HeaderItem,
  HeaderText,
  SubTitle,
  TypeTitle
} from "./styled";
import {
  tableCellTitle1,
  tableCellTitle2,
  tableCellTitle3,
  tableCellTitle4,
  tableCellTitle5,
  tableCellTitle6,
  tempColumns1,
  tempColumns2,
  tempColumns3,
  tempColumns4,
  tempColumns5,
  tempColumns6
} from "./pdf.cellTitle";

import echarts from 'echarts/lib/echarts'
import ReactEcharts from 'echarts-for-react';

import { Block } from "./Block";
import { CustomSelect } from "../../components/CustomSelect";
import { useMapUrl, usePdfData } from "./action";
import { CustomTable } from "./table";
import LoadingPage from "../../components/loading";
import { ColumnCenter, CopyRight, FlexCenter, RowCenter } from "../styled";
import { BackBtn } from "../../components/Button";
import { Document } from 'react-pdf';

export default () => {
  const { data, loading } = usePdfData();

  const { mapUrl } = useMapUrl();

  const [selected,setSelected] = useState('Star Early Test');

  const headerArr = [
    {
      label: '测试标准',
      value: 'CCSS Standard'
    },
    {
      label: '分数',
      value: data.scaledScore || '-'
    },
    {
      label: '蓝思值',
      value: data.lexileMeasure || '-'
    },
    {
      label: '测试日期',
      value: data.testDate || '-'
    },
  ];

  const selectList = [
    {
      key: 'Star Early Test',
      value: 'Star Early Test'
    },
    {
      key: 'Star Reading Test',
      value: 'Star Reading Test'
    },
    {
      key: 'Map Test',
      value: 'Map Test'
    },
    {
      key: 'Other',
      value: 'Other'
    }
  ];

  const option = {
    color: ['#006699', '#e5323e', '#4cabce',],
    tooltip: {
      trigger: 'axis',
      axisPointer: {
        type: 'shadow'
      }
    },
    legend: {
      data: data.trendDate.map(v=>`${v}测评结果`),
      textStyle: {
        fontSize: 12
      }
    },
    xAxis: [
      {
        type: 'category',
        axisTick: { show: false },
        data: ['字母基础', '音素基础', '自然拼读', '词法基础'],
        axisLabel: {
          show: true,
          textStyle: {
            fontSize: 18
          }
        }
      }
    ],
    yAxis: [
      {
        type: 'value',
        axisLabel: {
          show: true,
          textStyle: {
            fontSize: 18
          }
        }
      }
    ],
    series: [
      {
        name: `${data.trendDate[0]}测评结果`,
        type: 'bar',
        barGap: 0,
        data: data.trendValue[0]||[],
      },
      {
        name: `${data.trendDate[1]}测评结果`,
        type: 'bar',
        data: data.trendValue[1]||[],
      },
      {
        name: `${data.trendDate[2]}测评结果`,
        type: 'bar',
        data: data.trendValue[2]||[],
      }
    ]
  };


  return (
    <Wrap>
      <RowCenter>
        <BackBtn />
        <TypeTitle>测试类型：</TypeTitle>
        <CustomSelect
            initValue={selected}
            selectData={selectList}
            onChange={(key) => {
              setSelected(key);
            }}
          />
      </RowCenter>
      {
        selected === 'Map Test' ?
          <div>
            {
              mapUrl && <Document file={mapUrl}/>
            } 
          </div> :
          <div>
           <Title style={{ marginTop: 0 }}>早期阅读测试</Title>
           <FlexCenter>
             <HeaderWrap>
               {
                 headerArr.map((v, i, arr) => (
                   <HeaderItem
                     key={i}
                     style={i % 2 === 0 ? {} : { textAlign: 'right' }}
                   >
                     <span>
                       {v.label}
                     </span>
                     <HeaderText style={i === 1 ? { fontWeight:'bolder' } : {}}>
                       {v.value}
                     </HeaderText>
                   </HeaderItem>
                 ))
               }
             </HeaderWrap>
           </FlexCenter>
   
           <Line></Line>
   
           <Block score={data.scaledScore} />
   
           <Line></Line>
   
           <Title style={{marginTop:0,marginBottom:20}}>四大知识技能测评结果变化趋势</Title>
           <FlexCenter>
             <ReactEcharts option={option as echarts.EChartOption<echarts.EChartOption.Series>} theme="Imooc" style={{ height: '400px', width: '1400px' }} />  
           </FlexCenter>
   
           <Line></Line>
   
           <SubTitle>各知识技能点评测状态，红色表示需要紧急干预，黄色表示需要进一步观察，绿色表示该知识技能点已掌握</SubTitle>
           
           <CustomTable
             data={data.tableData1}
             tableCellTitle={tableCellTitle1}
             tempColumns={tempColumns1}
             title='字母基础'
           />
           <CustomTable
             data={data.tableData2}
             tableCellTitle={tableCellTitle2}
             tempColumns={tempColumns2}
             title='因素基础'
           />
           <CustomTable
             data={data.tableData3}
             tableCellTitle={tableCellTitle3}
             tempColumns={tempColumns3}
             title='自拼基础'
           />
           <CustomTable
             data={data.tableData4}
             tableCellTitle={tableCellTitle4}
             tempColumns={tempColumns4}
             title='高阶自拼'
           />
           <CustomTable
             data={data.tableData5}
             tableCellTitle={tableCellTitle5}
             tempColumns={tempColumns5}
             title='词法基础'
           />
           <CustomTable
             data={data.tableData6}
             tableCellTitle={tableCellTitle6}
             tempColumns={tempColumns6}
             title='其他'
           />
         </div>
          
      }
      <LoadingPage isLoading={loading} />
      <CopyRight>版权所有©2020 上海汤姆森信息技术有限公司©沪ICP备2020035728号</CopyRight>
    </Wrap>
  )
};
