import { message } from "antd";
import { useEffect, useState } from "react";
import { fetchIPictureData, ICourseData } from "../../services/IPicture";
import { getItem, StoreEnum } from "../../utils/store";
import { validateNum } from "../../utils/validate";


interface IData{
    [props:string]:ICourseData[]
}

export const useIPictureData = () => {
    const [loading, setLoading] = useState(true);

    const [countArr, setCountArr] = useState<number[]>([]);
    
    const [data, setData] = useState<IData>();
    const phone_number = getItem(StoreEnum.PHONE)||'15895901300';


    useEffect(() => {
        fetchIPictureData(phone_number).then((res) => {
            setCountArr(res.count_of_develop_items);
            setData({
                reading_literature: res.language_standards,
                reading_foundational_skills: res.reading_foundational_skills,
                writing: res.writing,
                speaking_listening: res.speaking_listening,
                language_standards: res.language_standards,
                reading_standards_informational_text:res.reading_standards_informational_text
            })
        }).finally(() => {
            setLoading(false);
        })
    }, []);
    
    return {
        loading,
        countArr,
        data,
    }
}

